import axios from "axios";
const API_URL = "https://todo--mern-app.herokuapp.com/api/user";

//class js object
class AuthService {
  login(email, password) {
    return axios.post(API_URL + "/login", { email, password });
  }
  logout() {
    localStorage.removeItem("user"); //localStorage
  }
  register(username, email, password) {
    return axios.post(API_URL + "/register", {
      username,
      email,
      password,
    });
  }
  getCurrentUser() {
    return JSON.parse(localStorage.getItem("user")); //loginlogout 
  }

  loginWithGoogle() {
    return axios.get(API_URL + "/google");
  }
}

export default new AuthService(); //AuthServiceclass new object 
